import React from "react";
import s from "./Cart.module.scss";
import { IProduct } from "../../scripts/mobx/productsStore";

interface IProps {
  positions: IProduct[];
  onClickDelButton: (id: string) => void;
}

const CartPositions = (props: IProps) => {
  return (
    <div className={s.CartPositions}>
      {props.positions.map((el, i) => {
        return (
          <div className={s.cartPosition} key={el.id + i}>
            {el.id}
            <div
              className={s.delCartPosition}
              onClick={() => props.onClickDelButton(el.id)}
            ></div>
          </div>
        );
      })}
    </div>
  );
};

export default CartPositions;
